@media screen and (max-width: 629px) {
    .MenuContainer{
        flex-direction: column;
        align-items: center;
    }
    .selectContainer span {
        font-size: 10px;
        margin-right: 10px;
    }
    .selectContainer{
        width: 100%;
        align-items: center;
        justify-content: flex-end;
        margin: 5px 0;
    }
    .searchField {
        width: 100%;
    }

}

@media screen and (min-width: 630px) {
    .MenuContainer{
        flex-direction: row;
        align-items: center;
    }
    .selectContainer span {
        margin-right: 10px;
        font-size: 14px;
    }
    .selectContainer{
        width: max-content;
        align-items: center;
    }

}
.MenuContainer {
    display: flex;
    justify-content: space-between;
    margin: 10px;
    background-color: whitesmoke;
}

.selectContainer {
    display: flex;
}

.searchField input {
    font-family: Montserrat, sans-serif;
    font-size: 14px;
}